import React, { useState } from "react";
import { Button, Image, Card, Row, Col } from "antd";
import ImageGallery from "./ImageGallery";
import loca from "../location.png"

// Import cc nh ca tng a im
var imagesContext = require.context(
    "../assets/van-mieu",
    false,
    /\.(png|jpe?g|svg)$/
);
const vanMieuImages = imagesContext
    .keys()
    .map((imagePath) => imagesContext(imagePath));

imagesContext = require.context(
    "../assets/hoa-lo",
    false,
    /\.(png|jpe?g|svg)$/
);
const hoaLoImages = imagesContext
    .keys()
    .map((imagePath) => imagesContext(imagePath));

imagesContext = require.context(
    "../assets/hoang-thanh",
    false,
    /\.(png|jpe?g|svg)$/
);
const hoangThanhImages = imagesContext
    .keys()
    .map((imagePath) => imagesContext(imagePath));

const TicketDetail = ({ location, locationDetails }) => {
    const [galleryVisible, setGalleryVisible] = useState(false);

    // Chn b nh da vo a im
    const getLocationImages = () => {
        switch (location) {
            case "van-mieu":
                return Object.values(vanMieuImages);
            case "hoa-lo":
                return Object.values(hoaLoImages);
            case "hoang-thanh":
                return Object.values(hoangThanhImages);
            default:
                return [];
        }
    };

    const address = {
        "van-mieu": "58 P. Quc T Gim, Vn Miu, ng a, H Ni",
        "hoa-lo": "1 P. Ho L, Trn Hng o, Hon Kim, H Ni",
        "hoang-thanh": "19c Hong Diu, in Bin, Ba nh, H Ni",
    };
    const chitiet = {
        "van-mieu":
            'Vn Miu  Quc T Gim l qun th di tch a dng, phong ph hng u ca thnh ph H Ni, nm  pha Nam kinh thnh Thng Long. Qun th kin trc Vn Miu  Quc T Gim bao gm: h Vn, khu Vn Miu  Quc T Gim v vn Gim, m kin trc ch th l Vn miu (ch Hn: ) - ni th Khng T, v Quc t gim (ch Hn: ) - trng i hc u tin ca Vit Nam. Khu Vn Miu  Quc T Gim c tng gch v bao quanh, pha trong chia thnh 5 lp khng gian vi cc kin trc khc nhau. Mi lp khng gian  c gii hn bi cc tng gch c 3 ca  thng vi nhau (gm ca chnh gia v hai ca ph hai bn). T ngoi vo trong c cc cng ln lt l: cng Vn Miu, i Trung, Khu Vn cc, i Thnh v cng Thi Hc.[1] Vi hn 700 nm hot ng  o to hng nghn nhn ti cho t nc. Ngy nay, Vn Miu  Quc T Gim l ni tham quan ca du khch trong v ngoi nc ng thi cng l ni khen tng cho hc sinh xut sc v cn l ni t chc hi th hng nm vo ngy rm thng ging. c bit, y cn l ni cc s t ngy nay n "cu may" trc mi k thi quan trng.',
        "hoa-lo":
            "Nh t Ha L hay nh pha Ha L l mt nh t do thc dn Php xy dng trn khu t xa thuc lng Ha L, nay c a ch: s 1 ph Ha L, phng Trn Hng o, qun Hon Kim, H Ni, Vit Nam. Nh t Ha L l mt a danh ni ting bi tng l ni giam gi rt nhiu nh cch mng ln ca Vit Nam trong Chin tranh ng Dng v phi cng M trong Chin tranh Vit Nam. a danh ny c B Vn ho Th thao v Du lch ca Vit Nam ra quyt nh s 1543-Q/VH ngy 18/6/1997 cng nhn l di tch lch s.",
        "hoang-thanh":
            "Hong thnh Thng Long (ch Hn: ; Hn-Vit: Thng Long Hong thnh) l qun th di tch gn vi lch s kinh thnh Thng Long - ng Kinh v tnh thnh H Ni bt u t thi k tin Thng Long (An Nam  h ph th k VII) qua thi inh - Tin L, pht trin mnh di thi L, Trn, L v thnh H Ni di triu Nguyn. y l cng trnh kin trc cc triu vua xy dng trong nhiu giai on lch s v tr thnh mt trong nhng di tch quan trng bc nht trong h thng cc di tch Vit Nam.",
    };

    const getThumbnail = () => {
        switch (location) {
            case "van-mieu":
                return vanMieuImages[0];
            case "hoa-lo":
                return hoaLoImages[0];
            case "hoang-thanh":
                return hoangThanhImages[0];
            default:
                return null;
        }
    };

    return (
        <>
            {location && (
                <Card hoverable style={{ width: "90%" }}>
                    <Row gutter={16}>
                        <Col xs={24} sm={12}>
                            <div>
                                <h1>{locationDetails[location]}</h1>
                                <Image width={'15px'} src={loca}/> <b>a ch: </b>{address[location]}
                                <div
                                    style={{
                                        maxHeight: "220px", // Gii hn chiu cao ca phn m t
                                        overflow: "auto", // Thm cun khi ni dung vt qu chiu cao
                                    }}
                                >
                                    <i style={{paddingLeft:"20px"}}>{chitiet[location]}</i>
                                </div>
                            </div>
                        </Col>
                        <Col xs={24} sm={12}>
                            <div style={{ textAlign: "center" }}>
                                <div
                                    style={{
                                        marginBottom: "20px",
                                        textAlign: "center",
                                    }}
                                >
                                    <Image
                                        src={getThumbnail()}
                                        height={"60%"}
                                        alt="nh i din a im"
                                        style={{
                                            borderRadius: "5px",
                                            maxHeight: "300px", // Gii hn chiu cao ca nh
                                        }}
                                    />
                                </div>
                                <Button
                                    type="primary"
                                    onClick={() => setGalleryVisible(true)}
                                >
                                    Xem tt c nh
                                </Button>
                            </div>
                        </Col>
                    </Row>

                    <ImageGallery
                        images={getLocationImages()}
                        visible={galleryVisible}
                        onClose={() => setGalleryVisible(false)}
                    />
                </Card>
            )}
        </>
    );
};

export default TicketDetail;
