import React from 'react';
import { Select } from 'antd';

const { Option } = Select;

const LocationSelect = ({ onChange }) => {
  return (<>
    <h1>Chn a im mun du lch</h1>
    <Select placeholder="Chn a im" style={{ width: 200 }} onChange={onChange}>
      <Option value="van-mieu">Vn Miu Quc T Gim</Option>
      <Option value="hoa-lo">Nh T Ha L</Option>
      <Option value="hoang-thanh">Hong Thnh Thng Long</Option>
    </Select></>
  );
};

export default LocationSelect;
