import React from "react";
import { Layout, Menu, Avatar, Dropdown, Button, Typography } from "antd";
import { UserOutlined, MenuOutlined } from "@ant-design/icons";
import { useNavigate } from "react-router-dom"; // S dng cho iu hng
import "../app.css";

const { Header } = Layout;
const { Title } = Typography;

const Navbar = () => {
    const navigate = useNavigate();

    // Menu cho dropdown ngn ng
    const languageMenu = (
        <Menu>
            <Menu.Item key="vi"> Ting Vit</Menu.Item>
            <Menu.Item key="en"> English</Menu.Item>
        </Menu>
    );

    // Menu cho dropdown ca 3 gch ngang
    const menuItems = (
        <Menu>
            <Menu.Item
                key="my-tickets"
                onClick={() => navigate("/my-tickets")} // ng dn n phn "V ca ti"
            >
                V ca ti
            </Menu.Item>
        </Menu>
    );

    return (
        <Header
            className="sticky-navbar"
            style={{
                display: "flex",
                alignItems: "center",
                backgroundColor: "#001529",
                padding: "0 20px",
            }}
        >
            <Title
                level={4}
                style={{
                    margin: 0,
                    flex: 1,
                    color: "#fff",
                    textAlign: "center",
                }}
                onClick={() => navigate("/")}
            >
                H Ni V m
            </Title>

            <div
                style={{
                    display: "flex",
                    alignItems: "center",
                    gap: "16px",
                }}
            >
                <Dropdown overlay={languageMenu} placement="bottomRight">
                    <Button
                        style={{
                            border: "none",
                            background: "transparent",
                            color: "#fff",
                        }}
                    >
                        
                    </Button>
                </Dropdown>

                <Dropdown overlay={menuItems} placement="bottomRight">
                    <Button
                        icon={<MenuOutlined />}
                        style={{
                            border: "none",
                            background: "transparent",
                            color: "#fff",
                        }}
                    />
                </Dropdown>
            </div>
        </Header>
    );
};

export default Navbar;
