import React, { useState } from "react";
import { Card, Button, Row, Modal } from "antd";
import { ButtonAmt } from "./ButtonAmt";
import CheckinDatePicker from "./DatePicker";
import { notification } from "antd";
import { useNavigate } from "react-router-dom"; // S dng cho iu hng

const Register = ({
    location,
    checkinDate,
    locationDetails,
    setCheckinDate,
    codedh,
}) => {
    const [cnt1, setCnt1] = useState(0);
    const [cnt2, setCnt2] = useState(0);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const navigate = useNavigate();

    const openNotification = (msg) => {
        notification.open({
            message: "Thng bo",
            description: msg,
            duration: 3,
        });
    };

    const showNotification = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        console.log(" xc nhn");

        // To i tng v mi
        const newTicket = {
            location: locationDetails[location],
            checkinDate,
            adultTickets: cnt1,
            studentTickets: cnt2,
            price: cnt1 * 160000 + cnt2 * 80000,
            orderCode: codedh,
        };

        // Ly mng v hin ti t localStorage (nu c)
        let tickets = JSON.parse(localStorage.getItem("tickets")) || [];

        // Thm v mi vo mng
        tickets.push(newTicket);

        // Lu li mng v vo localStorage
        localStorage.setItem("tickets", JSON.stringify(tickets));

        // ng modal sau khi lu
        setIsModalVisible(false);
        navigate("/my-tickets");
    };

    const handleCancel = () => {
        console.log(" hy");
        setIsModalVisible(false);
    };

    const checkingData = () => {
        if (!checkinDate) {
            openNotification("Vui lng chn ngy checkin!");
        } else if (cnt1 + cnt2 == 0) {
            openNotification("Vui lng chn loi v!");
        } else {
            showNotification();
        }
    };

    return (
        <>
            {location && (
                <Card hoverable style={{ width: "90%", marginTop: "15px" }}>
                    <h1>TY CHN GI DCH V</h1>
                    <b>
                        Chn gi dch v cho {locationDetails[location]} (tr em
                        di 5 tui c min ph v)
                    </b>
                    <div style={{ marginBottom: "20px" }}>
                        <CheckinDatePicker
                            onChange={(date) => setCheckinDate(date)}
                        />
                    </div>
                    <Row gutter={16} style={{ marginTop: "16px" }}>
                        <ButtonAmt
                            ct={"V ngi ln"}
                            price={["200.000", "160.000"]}
                            setCnt={setCnt1}
                        />
                        <ButtonAmt
                            ct={
                                "V hc sinh, sinh vin (gim 50% v ngi ln)"
                            }
                            price={["160.000", "80.000"]}
                            setCnt={setCnt2}
                        />
                    </Row>
                    <div style={{ textAlign: "right" }}>
                        <span>Tng tin: </span>
                        <b style={{ color: "red" }}>
                            {(cnt1 * 160000 + cnt2 * 80000).toLocaleString(
                                "vi-VN"
                            )}{" "}
                            
                        </b>
                        <br />
                        <div>
                            <Button
                                style={{ marginTop: "10px" }}
                                danger
                                type="primary"
                                onClick={checkingData}
                            >
                                t ngay
                            </Button>
                        </div>
                    </div>
                    <Modal
                        title="XC NHN N HNG"
                        visible={isModalVisible}
                        onOk={handleOk}
                        onCancel={handleCancel}
                        okText="Xc nhn"
                        cancelText="Thot"
                        style={{ textAlign: "center" }}
                        bodyStyle={{ textAlign: "left" }}
                    >
                        <b>a im: </b>
                        {locationDetails[location]}
                        <br />
                        <b>Ngy checkin: </b> {checkinDate}
                        <br />
                        <b>S v ngi ln: </b> {cnt1}
                        <br />
                        <b>S v hc sinh(sinh vin): </b> {cnt2}
                        <br />
                        <b>M n hng: </b>
                        {codedh}
                    </Modal>
                </Card>
            )}
        </>
    );
};

export default Register;
